import HomeElement from './Result.html';
import './Result.scss'
import {
  idCategory
} from '../Categories/Categories';
import {
  blockQuestionsByAuthor
} from '../Categories/Categories';
import {
  blockQuestionsByName
} from '../Categories/Categories';
import {
  resultCategory
} from '../Questions/Questions';
import {
  categoryType
} from '../Home/Home';
import {
  resultCategoryByPictures
} from '../Questions-pictures/Questions-pictures';

const innerResult = (arrAnswer, arrResult, block) => {
  arrAnswer.forEach((el, id) => {
    let newBlock = document.createElement('div');
    newBlock.classList.add('block__result-answer');
    newBlock.classList.add('result__no-color');
    newBlock.style.background = `url(./data/img/${el.imageNum}.jpg) center no-repeat`;
    newBlock.style.backgroundSize = 'contain';
    if (arrResult[id]) newBlock.classList.remove('result__no-color');
    block.append(newBlock);
  })
}


export class Result {
  constructor() {

  }

  async render() {
    return HomeElement;
  }

  async after_render() {
    const blockResult = document.querySelector('.block__result');
    let blockAnswer = categoryType === 'questions-artist' ? blockQuestionsByAuthor[idCategory].answer : blockQuestionsByName[idCategory].answer;
    let result = categoryType === 'questions-artist' ? resultCategory[idCategory] : resultCategoryByPictures[idCategory];
    innerResult(blockAnswer, result, blockResult)
  }

}