import ModalElement from './Modal.html';
import './Modal.scss'
import {
  Questions
} from '@/pages/Questions/Questions';
import {
  QuestionsPictures
} from '@/pages/Questions-pictures';
import {
  categoryType
} from '@/pages/Home/Home';


const questionsInstance = new Questions();
const questionsPicturesInstance = new QuestionsPictures();

export class Modal {
  constructor() {}

  async render() {
    return ModalElement;
  }

  async after_render() {
    const modalNext = document.querySelector('.modal__btn');
    const modalAuthor = document.querySelector('.modal__author');
    modalNext.addEventListener('click', () => {
      modalAuthor.classList.remove('modal__activ');
      if (categoryType === 'questions-artist') {
        questionsInstance.render();
        questionsInstance.after_render();
      } else {
        questionsPicturesInstance.render();
        questionsPicturesInstance.after_render();
      }
    })
  };
}