import SettingsElement from './Settings.html';
import './Settings.scss'



export const timerStart = (timeValue, falseQuestion, blockModal, blockTimer) => {
  let timerId = setInterval(() => {
    blockTimer.innerHTML = timeValue;
    if(timeValue === 0) {
      falseQuestion++;
      blockModal.classList.add('modal__activ');
    } 
    if(blockModal.classList.contains("modal__activ")) {
      clearInterval(timerId);
      blockTimer.innerHTML = 0;
    }
    --timeValue;
  }, 1000);
  setTimeout(() => clearInterval(timerId), 1000 * timeValue + 2000);
}

const audioProgress = () => {
  progress = Math.floor(audio.currentTime) / (Math.floor(audio.duration) / 100);
  progressBar.value = progress;
  let time = audioTimeText(audio.currentTime);
  audioTime.innerHTML = `${playList[playNum].duration} / ${time}`;
}


let settingTime = false;
let volumeValue = 50;
let timeToAnswer = 5;


export class Settings {
  constructor() { }

  async render() {
    return SettingsElement;
  }

  async after_render() {
    const btnTimeInput = document.querySelector('.btn__time-input');
    const time = document.getElementById('volume-range');
    const inputTimeBtn = document.querySelectorAll('.input-time-btn');
    const timeAnswer = document.querySelector('.time-answer')
    const saveSettings = document.querySelector('.setting-save');
    const spanTime = document.querySelector('.span-time');
    btnTimeInput.checked = Boolean(localStorage.getItem('settingTime')) || settingTime;
    time.value = localStorage.getItem('volumeValue') || volumeValue;
    timeAnswer.value = localStorage.getItem('timeToAnswer') || timeToAnswer;

    localStorage.getItem('settingTime') ? spanTime.innerHTML = 'On' :  spanTime.innerHTML = 'Off';
    btnTimeInput.addEventListener('change', () => {
      btnTimeInput.checked ? settingTime = 1 : settingTime = '';
      btnTimeInput.checked ? spanTime.innerHTML = 'On' :  spanTime.innerHTML = 'Off';
    });
    time.addEventListener('change', () => {
      volumeValue = time.value;
    });
    inputTimeBtn.forEach(el => {
      el.addEventListener('click', () => timeToAnswer = timeAnswer.value)
    });
    saveSettings.addEventListener('click', () => {
      localStorage.setItem('timeToAnswer', timeToAnswer);
      localStorage.setItem('settingTime', settingTime);
      localStorage.setItem('volumeValue', volumeValue);
    })
  };
}